import React from 'react';
import styled from 'styled-components';
import Preview from '../../components/Preview/Preview';

const HomeContainer = styled.div`
  flex: 1;
  background: #13111b;
  padding: 0;
  margin: 0;
`;

const MainSection = styled.section`
  background: linear-gradient(135deg, #13111b 0%, #17151f 100%);
`;

const Banner = styled.div`
  height: 70vh;
  position: relative;
  background: #13111b;
  display: flex;
  align-items: center;
  justify-content: center;
  
  @media (max-width: 768px) {
    height: 50vh;
  }
`;

const BannerContent = styled.div`
  max-width: 800px;
  text-align: center;
  padding: 2rem;
  z-index: 1;
`;

const Title = styled.h1`
  font-size: 3rem;
  color: #9b6dff;
  margin-bottom: 1.5rem;

  @media (max-width: 768px) {
    font-size: 2rem;
  }
`;

const Description = styled.p`
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  max-width: 600px;
  margin: 0 auto;

  @media (max-width: 768px) {
    font-size: 1rem;
  }
`;

const VideoGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1.5rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;

  @media (max-width: 768px) {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    gap: 1rem;
    padding: 1rem;
  }
`;

const CategoryTitle = styled.h2`
  font-size: 2rem;
  color: #9b6dff;
  margin: 2rem 0 1rem;
  padding: 0 2rem;
  max-width: 1200px;
  margin: 2rem auto 1rem;

  @media (max-width: 768px) {
    font-size: 1.5rem;
    padding: 0 1rem;
  }
`;

function Home() {
  const [videos, setVideos] = React.useState([]);
  const [selectedVideo, setSelectedVideo] = React.useState(null);
  const [selectedCategory, setSelectedCategory] = React.useState(null);
  const categories = ['Pelculas', 'Series', 'Documentales', 'Anime'];
  const [isPlaying, setIsPlaying] = React.useState(false);
  const categoryRefs = React.useRef({});
  const [bannerVideo] = React.useState({
    url: 'https://www.youtube.com/watch?v=O6JxZxIzRGc',
    title: 'RECOMIENDA TU TRAILER FAVORITO',
    description: 'Sube tu trailer favorito para que podamos conocer tus pelculas, animes, series y documentales que quieres que todos veamos.',
    thumbnail: 'https://i.ytimg.com/vi/O6JxZxIzRGc/maxresdefault.jpg'
  });

  React.useEffect(() => {
    fetchVideos();
  }, []);

  const fetchVideos = async () => {
    try {
      const response = await axios.get(API_URL);
      setVideos(response.data);
    } catch (error) {
      console.error('Error fetching videos:', error);
    }
  };

  const scrollToCategory = (category) => {
    const element = document.getElementById(category.toLowerCase());
    if (element) {
      element.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
      setSelectedCategory(category);
    }
  };

  const handleEdit = (video) => {
    navigate('/new-video', { state: { editVideo: video } });
  };

  const handleDelete = async (videoId) => {
    try {
      await axios.delete(`${API_URL}/${videoId}`);
      await fetchVideos();
    } catch (error) {
      console.error('Error deleting video:', error);
    }
  };

  const renderVideosByCategory = (category) => {
    const filteredVideos = videos.filter(video => video.category === category);
    if (filteredVideos.length === 0) return null;

    return (
      <div id={category.toLowerCase()} key={category} style={{ scrollMarginTop: '100px' }}>
        <CategoryTitle>{category}</CategoryTitle>
        <VideoGrid>
          {filteredVideos.map(video => (
            <Preview
              key={video.id}
              image={video.thumbnail}
              title={video.title}
              description={video.description}
              onClick={() => setSelectedVideo(video)}
            />
          ))}
        </VideoGrid>
      </div>
    );
  };

  return (
    <HomeContainer>
      <MainSection>
        <Banner>
          <BannerContent>
            <Title>TRILERFLIX</Title>
            <Description>
              Sube tu trailer favorito para que podamos conocer tus pelculas, animes, series y
              documentales que quieres que todos veamos.
            </Description>
          </BannerContent>
        </Banner>
        <CategoryTitle>ltimos trailers</CategoryTitle>
        <VideoGrid>
          {categories.map(category => renderVideosByCategory(category))}
        </VideoGrid>
      </MainSection>
      {selectedVideo && (
        <VideoModal
          isOpen={!!selectedVideo}
          onClose={() => setSelectedVideo(null)}
          video={selectedVideo}
          API_URL={API_URL}
        />
      )}
    </HomeContainer>
  );
}

export default Home;
