import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import styled from 'styled-components';

const HeaderContainer = styled.header`
  background: rgba(19, 17, 27, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(155, 109, 255, 0.1);
  position: sticky;
  top: 0;
  z-index: 1000;
`;

const HeaderContent = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;

  @media (max-width: 768px) {
    padding: 1rem 0.5rem;
  }
`;

const Logo = styled.h1`
  color: #9b6dff;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
  cursor: pointer;
  transition: color 0.3s ease;

  &:hover {
    color: #8257e6;
  }
`;

const ButtonGroup = styled.div`
  display: flex;
  gap: 1rem;
  align-items: center;

  @media (max-width: 480px) {
    gap: 0.5rem;
  }
`;

const Button = styled.button`
  background: ${props => props.primary ? '#9b6dff' : 'transparent'};
  color: white;
  border: ${props => props.primary ? 'none' : '2px solid #9b6dff'};
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;

  &:hover {
    background: ${props => props.primary ? '#8257e6' : 'rgba(155, 109, 255, 0.1)'};
    transform: translateY(-2px);
  }

  @media (max-width: 480px) {
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }
`;

const Header = () => {
  const location = useLocation();

  return (
    <HeaderContainer>
      <HeaderContent>
        <Logo>TRILERFLIX</Logo>
        <ButtonGroup>
          <Button 
            to="/" 
            primary={location.pathname === '/' ? 1 : 0}
          >
            Hogar
          </Button>
          <Button 
            to="/new-video" 
            primary={location.pathname === '/new-video' ? 1 : 0}
          >
            Nuevo Video
          </Button>
        </ButtonGroup>
      </HeaderContent>
    </HeaderContainer>
  );
};

export default Header;
